ul,
li {
    list-style: none;
    padding: 0;
    margin: 0;
}

.nav {
    
    position: absolute;
    top: 1rem;
    left: 3rem;
    padding: 15px 0;
    z-index: 11;
    user-select: none;
    .menu {
        display: flex;
    }

    ul {
        display: flex;
        align-items: center;
        text-transform: uppercase;
        letter-spacing: 3px;
    }

    li {
        display: flex;
        margin: 0 1rem;
        a {
            font-size: calc(0.3rem + 10px);
            font-weight: 300;
            text-decoration: none;
            outline: none;
            white-space: nowrap;
            
        }
    }
    
}


@media (max-width: 576px) {
    .nav {
        left: 50%;
        transform: translateX(-50%);
    }
}





