.portfolio {
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100%;
    min-height: 100vh;
    background: #111;
    overflow-y: auto;
    overflow-x: hidden;
    
}
.portfolio-content {
    color: #fff;
    font-size: 4rem;
    font-weight: 700;
    max-width: 1000px;
    width: 100%;
    margin: 100px auto;
    line-height: 4rem;
    padding: 15px;
}

.portfolio-content__title{
    font-size: 5rem;
    position: relative;
    letter-spacing: 2px;
    white-space: nowrap;
}
.portfolio-content-item {
    margin: 50px 0;
    padding:15px 15px 15px 25px;
    position: relative;
    border: 2px solid #fff;
    box-sizing: border-box;
   
}
.portfolio-content-item__bg {
    position: absolute;
    left: 0;
    top: 0;
    width: 4px;
    height: 100%;
    background: #fff;
   

}


.portfolio-content-item__title {
    font-size: 3.5rem;
    line-height: 3.5rem;
 
}
.portfolio-content-item__site {
    font-size: 2rem;
    line-height: 3rem;
    text-decoration: underline;
}

.portfolio-content-item__description {
    font-size: 2rem;
    line-height: 2.5rem;
}

.portfolio-content-item__features {
    font-size: 2rem;
}
.portfolio-content-item__features span{
    background: #444;
    padding: 0 5px;
}

