
/* Block Scroll */

::-webkit-scrollbar {
    width: 15px;
}

::-webkit-scrollbar-track {
    background: #111;

}
::-webkit-scrollbar-thumb {
    background: #fff;
    border: 6px solid #111;
}

 /* /Block Scroll */ 

 .lang {
    position: absolute;
    bottom: 0;
    right: 0;

    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: 200;
    color: #fff;
 
    text-align: center;
    line-height: 30px;
    border: 2px solid rgba(255, 255, 255, 0.75);
    width: 40px;
    margin: 0 15px 10px 0;
    cursor: pointer;
    user-select: none;
    transition-duration: 0.3s ;
}

.lang:hover {
    background: #fff;
    color: #111;
}

 .copyright {

    font-weight: 100;
    color: rgba(255, 255, 255, 0.5);
    font-size: 16px;
    letter-spacing: 2px;
    z-index: 10;
    user-select: none;
}
 .copy-right {
    font-weight: 100;
    color: rgba(255, 255, 255, 0.5);
    font-size: 16px;
    letter-spacing: 2px;
    user-select: none;

}


.bg-grey {
  background: rgba(17, 17, 17, 0.85);
  transition-delay: 1000ms;
}


.page-appear {
  opacity: 0;
}
.page-appear-active {
  opacity: 1;
  transition-delay: 500ms;
}
.page-enter {
  opacity: 0;
}
.page-enter-active {
  opacity: 1;
  transition-delay: 1000ms;
}
.page-exit {
  opacity: 1;
}
.page-exit-active {
  opacity: 0;

}