body {
    background: #111;
}
.slider {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

.slide-text-line {
    overflow: hidden;
}



.slider__inner {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}


.slider canvas {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
}

.slide {
    position: absolute;
    left: 0;
    top: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
}

.slide-text {
    position: absolute;
    bottom:100px;
    left: 9rem;
    z-index: 10;
    font-size: 4rem;
    text-transform: uppercase;
    transform-origin: top;
    line-height: 1.075;
    color: #fff;
    font-weight: 400;
}




.copyright {
    position: absolute;
    bottom: 0;
    left: 0.2rem;
}
.scroll{
    font-size: 16px;
    position: absolute;
    bottom: 40px;
    left: 50%;
    transform: translateX(-50%);
    color: rgba(255, 255, 255, 0.75);
    font-weight: 300;
    letter-spacing: 3px;
    user-select: none;
}
@media (max-width: 576px) {
    
    .slide-text {
        left: 4rem;
    }
    .copyright {
        font-size: 12px;
    }
}
