.about {
    position: absolute;
    left: 0;
    top: 0;
    width: 100vw;
    height: 100%;
    min-height: 100vh;
    background: #111;
    overflow-x: auto;
  
}


.about-content {
    color: #fff;
    font-size: 4rem;
    font-weight: 700;
    max-width: 1000px;
    width: 100%;
    margin: 100px auto;
    line-height: 4rem;
    padding: 1rem;
 
}
.about-content a {
    text-decoration: underline;
}

.about-content__title{
    text-align: center;
    font-size:  5rem;
    letter-spacing: 1px;
}

.about-image {
    width: calc(37*(5px + 1vw));
    height: auto;
    margin: 3rem;
}


.about-image img{
    width: 100%;
    height: 100%;
}
